.userFillIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.menu {
  position: relative;
  line-height: 20px;
  font-weight: 500;
}
.menuItem {
  border-radius: var(--br-29xl);
  border: 2px solid var(--primary-50);
  overflow: hidden;
  color: var(--neutral-100);
}
.menuItem,
.menuItem1 {
  width: 212px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-base);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.settingSidebar,
.settingsMenu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.settingsMenu {
  gap: var(--gap-9xs);
}
.settingSidebar {
  width: 212px;
}
.editProfile {
  align-self: stretch;
  position: relative;
  letter-spacing: -0.01em;
  line-height: 56px;
}
.frameChild {
  border-radius: 50%;
  width: 112px;
  height: 112px;
  object-fit: cover;
}
.add,
.fileIcon,
.frameChild {
  position: relative;
}
.fileIcon {
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.add {
  line-height: 24px;
  font-weight: 500;
}
.buttonOutline {
  border-radius: var(--br-3xs);
  border: 2px solid var(--neutral-30);
  box-sizing: border-box;
  width: 186px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-xs) var(--padding-5xl);
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
}
.jpgOrPng {
  margin: 0;
}
.atLeast800x800Container {
  align-self: stretch;
  position: relative;
  font-size: var(--body-sm-regula-size);
  line-height: 20px;
  font-weight: 500;
  color: var(--neutral-60);
}
.buttonOutlineParent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.avatar,
.ellipseParent {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
}
.ellipseParent {
  flex-direction: row;
  align-items: center;
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--body-regula-size);
  color: var(--neutral-90);
}
.avatar {
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-xs);
  text-align: center;
}
.enterEmailAddress {
  position: relative;
  line-height: 24px;
}
.userFillParent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.emailIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  opacity: 0;
}
.input {
  border-radius: var(--br-3xs);
  background-color: var(--neutral-5);
  border: 1px solid var(--neutral-30);
  display: flex;
  flex-direction: row;
  padding: var(--padding-sm) var(--padding-base);
  align-items: center;
  justify-content: space-between;
  text-align: center;
  font-size: var(--body-regula-size);
}
.email,
.input,
.name {
  align-self: stretch;
}
.name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.email {
  position: relative;
  line-height: 20px;
  font-weight: 500;
}
.emailFillIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.div {
  position: relative;
  line-height: 20px;
  font-weight: 500;
  color: var(--neutral-50);
}
.bioParent {
  flex-direction: row;
  justify-content: space-between;
}
.avatarParent,
.bioParent,
.input2 {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.input2 {
  border-radius: var(--br-3xs);
  background-color: var(--neutral-5);
  border: 1px solid var(--neutral-30);
  box-sizing: border-box;
  height: 96px;
  flex-direction: row;
  padding: var(--padding-xs) var(--padding-base);
  justify-content: space-between;
  text-align: center;
  font-size: var(--body-regula-size);
  color: var(--neutral-60);
}
.avatarParent {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.addIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.button {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--primary-50);
  display: flex;
  flex-direction: row;
  padding: var(--padding-xs) 0;
  align-items: center;
  justify-content: center;
  gap: var(--gap-xs);
  text-align: center;
  font-size: var(--body-regula-size);
  color: var(--neutral-01-100);
}
.editProfile1 {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-13xl);
  font-size: var(--body-sm-regula-size);
}
.editProfile1,
.editProfileon,
.settingsModal {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.editProfileon {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  gap: var(--gap-13xl);
  text-align: left;
  font-size: var(--h3-bold-size);
  color: var(--neutral-100);
}
.settingsModal {
  position: absolute;
  top: calc(50% - 400px);
  left: calc(50% - 384px);
  border-radius: var(--br-5xl);
  background-color: var(--neutral-01-100);
  width: 768px;
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-29xl);
  box-sizing: border-box;
  gap: var(--gap-29xl);
}
.settings {
  position: relative;
  border-radius: var(--br-xl);
  background-color: var(--color-white);
  width: 100%;
  height: 1024px;
  overflow: hidden;
  text-align: center;
  font-size: var(--body-sm-regula-size);
  color: var(--neutral-60);
  font-family: var(--body-sm-regula);
}
