@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --body-sm-regula: Roboto;
  --base-2-semibold: Inter;

  /* font sizes */
  --body-regula-size: 16px;
  --body-sm-regula-size: 13px;
  --h3-bold-size: 31px;
  --h5-medium-size: 20px;
  --base-2-semibold-size: 14px;
  --h6-size: 18px;

  /* Colors */
  --color-white: #fff;
  --neutral-01-100: #fefefe;
  --neutral-06-100: #232627;
  --neutral-90: #191a1a;
  --neutral-07-100: #141718;
  --neutral-100: #0c0d0d;
  --neutral-06-50: rgba(35, 38, 39, 0.5);
  --color-gray-200: rgba(254, 254, 254, 0.4);
  --color-gray-100: rgba(20, 23, 24, 0.75);
  --neutral-5: #f9f9fa;
  --neutral-20: #f2f2f3;
  --neutral-03-100: #e8ecef;
  --neutral-50: #b0b3b5;
  --neutral-70: #4a4d4f;
  --neutral-80: #313435;
  --primary-50: #4386f4;
  --primary-01-100: #0084ff;
  --neutral-60: #6c7275;
  --neutral-30: #e5e6e6;
  --color-goldenrod: #e8c547;
  --neutral-40: #cacdce;

  /* Gaps */
  --gap-29xl: 48px;
  --gap-13xl: 32px;
  --gap-base: 16px;
  --gap-xs: 12px;
  --gap-9xs: 4px;
  --gap-5xs: 8px;
  --gap-5xl: 24px;
  --gap-xl: 20px;

  /* Paddings */
  --padding-29xl: 48px;
  --padding-5xl: 24px;
  --padding-3xs: 10px;
  --padding-base: 16px;
  --padding-5xs: 8px;
  --padding-xl: 20px;
  --padding-xs: 12px;
  --padding-9xs: 4px;
  --padding-21xl: 40px;
  --padding-11xs: 2px;
  --padding-sm: 14px;

  /* border radiuses */
  --br-xl: 20px;
  --br-5xl: 24px;
  --br-7xs: 6px;
  --br-29xl: 48px;
  --br-3xs: 10px;
  --br-31xl: 50px;
  --br-xs: 12px;
  --br-5xs: 8px;
  --br-11xs: 2px;
  --br-10xs-5: 2.5px;
}
